import * as React from 'react';

export interface IWithConnectorState {
  ConnectorForm?: any;
  loading: boolean;
  error: boolean;
}

export interface IWithConnectorForm {
  connectorId: string;
  children(props: IWithConnectorState): any;
}

export class WithConnectorForm extends React.Component<IWithConnectorForm, IWithConnectorState> {
  public state = {
    loading: true,
    error: false
  };

  public async componentDidMount() {
    try {
      this.setState({
        loading: true
      });
      const ConnectorForm = await import(`@syndesis/connector-${this.props.connectorId}`);
      console.log("???", ConnectorForm);
      this.setState({
        ConnectorForm: ConnectorForm,
        loading: false
      })
    } catch(e) {
      this.setState({
        error: true,
        loading: false
      });
    }
  }

  public render() {
    return this.props.children(this.state);
  }
}
